label{
  color : black !important;
}

input, textarea {
    display: block;
    width: 100%;
    padding: .375rem .75rem;
    font-size: 1rem;
    line-height: 1.5;
    color: #495057;
    background-color: #fff;
    background-clip: padding-box;
    border: 1px solid #ced4da;
    border-radius: .25rem;
    margin-bottom: .8rem;
  }

  label {
    margin-bottom: .65rem;
    display: block;
    padding-left: 2px;
    color:black;
    font-weight: 600;
    letter-spacing: 0.75px;
  }
  textarea {
    height: 125px;
  }
  button {
    display: inline-block;
    font-weight: 400;
    text-align: center;
    white-space: nowrap;
    vertical-align: middle;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    border: 1px solid transparent;
    padding: .55rem 1.5rem;
    font-size: 1.2rem;
    line-height: 1.5;
    border-radius: .25rem;
    color: #fff;
    background-color: #FD6413;
    cursor: pointer;
  }
  button:disabled {
    background-color: #777;
  }
  .form-bottom {
    margin-top: 25px;
    margin-bottom: 25px;
    display: flex;
    align-items: center;
  }
  button {
    margin-right: 5px;
  }
  .message {
    width: 100%;
    position: relative;
    padding: .75rem 1.25rem;
    margin-bottom: 1rem;
    border: 1px solid transparent;
    border-radius: .25rem;
    opacity: 0;
    transition: opacity 2s linear ;
  }

  .message.success {
    color: #155724;
    background-color: #d4edda;
    border-color: #c3e6cb;
    opacity: 1;
    transition: opacity 2s linear ;
  }

  .message.error {
    color: #721c24;
    background-color: #f8d7da;
    border-color: #f5c6cb;
    opacity: 1;
    transition: opacity 2s linear ;
  }

form {
